
    import { storeKey } from '@/store'
    import { computed, defineComponent } from 'vue'
    import { useStore } from 'vuex'

    export default defineComponent({
        name: 'SocialMedia',
        components: {
        },
        setup() {
            const store = useStore(storeKey)

            const socials = computed(() => store.state.socials)
            
            return {
                socials
            }
        }
    })
