
    import { storeKey } from '@/store'
    import { computed, defineComponent } from 'vue'
    import { useStore } from 'vuex'

    export default defineComponent({
        name: 'ReferenceList',
        components: {},
        setup() {
            const store = useStore(storeKey)
            const refs = computed(() => store.state.references)
            
            return {
                refs
            }
        }
    })
